import { format } from "date-fns";
import { ColumnFilter } from "./ColumnFilter";

const handleCellClick = (e) => {
  e.target.style.backgroundColor = "red";
  console.log(e);
};

export const dataTable = () => {
  const headerCells = [];
  for (let h = 6; h < 24; h++) {
    for (let m of ["00", "15", "30", "45"]) {
      const date = new Date(0, 0, 0, h, m, 0, 0); //   Date   15- 
      const dateString = format(date, "HH:mm"); //       HH:MM
      // headerCells.push(<th key={dateString}>{dateString}</th>);
      const formatDateString =
        dateString[0] == 0 ? dateString.slice(1) : dateString;
      headerCells.push({
        Header: <span className="time-column">{formatDateString}</span>,
        accessor: formatDateString,
        // getTdProps: (rowInfo) => {
        //   return {
        //     onClick: (event) => handleCellClick(event, rowInfo),
        //   };
        // },
      });
    }
  }
  return headerCells;
};

export const COLUMNS = [
  {
    Header: "Id",
    Footer: "Id",
    accessor: "id",
    Filter: ColumnFilter,
  },
  {
    Header: " ",
    Footer: "Full Name",
    accessor: "full_name_of_the_employee",
    Filter: ColumnFilter,
  },
  {
    Header: "Last Name",
    Footer: "Last Name",
    accessor: "last_name",
    Filter: ColumnFilter,
  },
  {
    Header: "Date of Birth",
    Footer: "Date of Birth",
    accessor: "date_of_birth",
    Cell: ({ value }) => {
      return format(new Date(value), "dd/MM/yyyy");
    },
    Filter: ColumnFilter,
  },
  {
    Header: "Country",
    Footer: "Country",
    accessor: "country",
    Filter: ColumnFilter,
  },
  {
    Header: "Phone",
    Footer: "Phone",
    accessor: "phone",
    Filter: ColumnFilter,
  },
];

export const GROUPED_COLUMNS = [
  {
    Header: "",
    columns: [
      {
        Header: "Id",
        accessor: "id",
      },
      {
        Header: <span className="name-column"> </span>,
        accessor: "full_name_of_the_employee",
      },
      {
        Header: "",
        accessor: "shift",
      },
    ],
  },
  {
    Header: "",
    columns: dataTable(),
  },
];
